import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppComponent } from './app.component';
import { MainLayoutComponent } from './main-layout/main-layout.component';
import { HomePageComponent } from './home-page/home-page.component';
import { EmptyLayoutComponent } from './empty-layout/empty-layout.component';
import { FinalBookingComponent } from './final-booking/final-booking.component';
import { SearchResultsComponent } from './search-results/search-results.component';
import { FirmProfileComponent } from './firm-profile/firm-profile.component';
import { BookingSuccessComponent } from './booking-success/booking-success.component';
import { SearchResultsService } from './search-results/search-results.service';
import { NavigationService } from './navigation.service';


const routes: Routes = [
  {
    path:'' , component:HomePageComponent, data: {animation: 'HomePage', flag:'true'}
  },
{
   path: 'assistance', loadChildren: './assistance/assistance.module#AssistanceModule'
   },
   {
     path:'empty', component:EmptyLayoutComponent
   },
   {
    path:'booking', component:FinalBookingComponent
  },
  {
    path:'search', component:SearchResultsComponent
  },
  {
    path:'firm-profile', component:FirmProfileComponent
  },
  {
    path: 'success', component: BookingSuccessComponent
  },

];

@NgModule({
  declarations: [
    AppComponent,
    MainLayoutComponent,
    HomePageComponent,
    EmptyLayoutComponent,
    FinalBookingComponent,
    SearchResultsComponent,
    FirmProfileComponent,
    BookingSuccessComponent,
    
  ],
  imports: [
    BrowserModule, RouterModule.forRoot(routes), BrowserAnimationsModule
  ],
  providers: [SearchResultsService, NavigationService],
  bootstrap: [AppComponent]
})
export class AppModule { }
